import React from 'react';
import './footer.css'

const Footer = () => {

	const openInNewTab = url => {
		window.open(url, '_blank', 'noopener,noreferrer');
	};

	return (
		<footer className="footer">
			<div className="container">
				<div className="footer-content">
					<div className="icons-wrapper">
						<a className="icon-link" onClick={() => openInNewTab("https://www.instagram.com/yiiamee/")}><img
							src="https://cdn-icons-png.flaticon.com/512/1419/1419515.png" alt="instagram icon" /></a>
						<a className="icon-link" onClick={() => openInNewTab("https://www.linkedin.com/in/mykhailo-kukharskiy-585b5122b/")}><img
							src="https://cdn-icons-png.flaticon.com/512/3536/3536569.png" alt="linkedIn icon"/></a>
					</div>
					<div className="icons-wrapper">
						<img src="https://cdn-icons-png.flaticon.com/512/12/12096.png" alt="superhero icon" className="footer-icon"/>
					</div>
					<div className="icons-wrapper">
						<a className="icon-link" onClick={() => openInNewTab("https://github.com/yiiameeMich")}><img
							src="https://cdn-icons-png.flaticon.com/512/3291/3291695.png" alt="github icon"/></a>
						<a className="icon-link"
						   onClick={() => openInNewTab("https://mail.google.com/mail/u/0/?pc=ru-ha-emea-ua-bk#inbox?compose=GTvVlcSBnNSqlDJKcFSrccjCzJwvTLBMGgvMNBZPcXjVLxFmlHnRCTHKwBLHCHlvsnjbDgkdbgcQX")}>
							<img src="https://cdn-icons-png.flaticon.com/512/60/60543.png" alt="email icon"/></a>
					</div>
				</div>
			</div>
		</footer>
	)
}

export default Footer;